/* eslint-disable @typescript-eslint/no-unused-vars */
import React from "react";
import styled from "styled-components";
import { Tag, Flex, Heading, Image } from "@pancakeswap-libs/uikit";
import { NoFeeTag } from "components/Tags";

export interface ExpandableSectionProps {
  lpLabel?: string
  multiplier?: string
  risk?: number
  depositFee?: number
  farmImage?: string
  tokenSymbol?: string
  token1?: string
  token2?: string
}

const Wrapper = styled(Flex)`
  svg {
    margin-right: 0.25rem;
  }
`;

const MultiplierTag = styled(Tag)`
  margin-left: 4px;
`;

const ImageContainer = styled.div`
  display: flex;
  margin: 8px 32px 0 32px;

  &:hover {
    .spin-cw {
      transform: rotateZ(360deg);
    }

    .spin-ccw {
      transform: rotateZ(-360deg);
    }
  }

  .spin-cw {
    transition: 0.4s ease-in-out;
  }

  .spin-ccw {
    transition: 0.4s ease-in-out;
  }
`;

const ImageLeft = styled.div`
  display: flex;
  position: relative;
  left: 12px;
`;

const ImageRight = styled.div`
  display: flex;
  position: relative;
  right: 12px;
`;

const CardHeading: React.FC<ExpandableSectionProps> = ({
  lpLabel,
  multiplier,
  risk,
  farmImage,
  tokenSymbol,
  depositFee,
  token1,
  token2
}) => {
  return (
    <Wrapper justifyContent="center" alignItems="center" mb="12px">
      <div style={{display: "flex", flexDirection: "column", alignItems: "center"}}>
        <Flex flexDirection="column" alignItems="center">
          {depositFee === 0 ?
            <NoFeeTag />
            :
            <MultiplierTag variant="secondary">{multiplier}</MultiplierTag>
          }

          <Heading mb="4px">{lpLabel}</Heading>
        </Flex>

        <ImageContainer>
          <ImageLeft className="spin-cw"><img src={`/images/tokens/${token1}.png`}  alt={token1} /></ImageLeft>
          <ImageRight className="spin-ccw"><img src={`/images/tokens/${token2}.png`} alt={token2} /></ImageRight>
        </ImageContainer>
      </div>
    </Wrapper>
  );
};

export default CardHeading;
